export default {
	data() {
		return {
			essay:'',
			llist:[],
			rlist:[],
			ltop: 0,
			rtop: 0
		}
	},
	created() {
		this.getData()
	},
	methods: {
		getData(){
			let start = 0
			let num =20
			let url




			url = `${this.$store.state.baseURL}/m/essay_list`

			
			let data = {
				start: start,
				num: num
			}
			let _this = this
			this.$store.dispatch('getEssayList',{url,data})
			.then((res)=>{
				console.log(res)
				this.essay = this.$store.state.essay.list
				this.llist.push(this.essay[0])
				this.essay.shift()
			})
		},
		onImgLoad(flag,i,pic){
			if(!this.essay.length){
				return
			}
			let id = `#${flag}${i}`
			const query = uni.createSelectorQuery().in(this);
			query.select(id).boundingClientRect(data => {
				if(flag === 'l'){
					this.ltop += data.height
				}
				else{
					this.rtop += data.height
				}
				if(this.ltop > this.rtop){
					this.rlist.push(this.essay[0])
				}
				else{
					this.llist.push(this.essay[0])
				}
				this.essay.shift()
			}).exec();
		},
		onPullDownRefresh() {
			this.essay = []
			this.rlist = []
			this.llist = []
			setTimeout( ()=> {
				
				uni.stopPullDownRefresh();
			}, 1000);
			this.getData()
		},
		addCom(){
			uni.navigateTo({
				url:'/pagesA/article/article'
			})
		}
	}
}
